body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  max-width: 1400px;
  margin: auto;
  background-color: rgb(231, 231, 231);
}
.NewsItem {
    display: grid;
    grid-template-rows: 1fr .5fr;
    grid-template-columns: 40px 1fr;
    margin-bottom: 5px;
    margin-top: 5px;
}

.post-number {
    grid-row: 1/3;
    grid-column: 1/2;
    text-align: center;
    margin-right: 5px;
}

.post-name {
    grid-row: 1/2;
    grid-column: 2/3;
    margin: 0;
}

.post-name a {
    text-decoration: none;
    color: black;
}

.post-name a:visited {
    color: rgb(114, 114, 114);
}

.post-description {
    grid-row: 2/3;
    grid-column: 2/3;
    font-size: 13px;
    color: rgb(68, 68, 68);
}

.post-description a {
    text-decoration: none;
    color: rgb(68, 68, 68);
}

.post-description span {
    margin-right: 10px;
}

.NewsItem a:hover {
    text-decoration: underline;
}


.post-view-comments,
.post-time {
    white-space: nowrap;
    display: inline-block;
}

.post-text {
    grid-column: 1/3;
    margin-top: 10px;
}

.post-text,
.post-text p {
    overflow: auto;
} 
.user-about,
.user-about p {
    overflow: auto;
}

.user-name {
    grid-row: 1/2;
    grid-column: 2/3;
    font-weight: 700;
}

.user-headlines {
    display: grid;
    grid-template-rows: repeat(3, 40px) 1fr;
    grid-row: 1/4;
    margin: 0;
}

.User {
    display: grid;
    grid-template-columns: 60px 1fr;
    grid-template-rows: repeat(3, 40px) 1fr;
    grid-column-gap: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.user-created {
    grid-row: 2/3;
    grid-column: 2/3;
}

.user-karma {
    grid-row: 3/4;
    grid-column: 2/3;
}

.user-about {
    grid-row: 4/5;
    grid-column: 2/3;
}
.comment-user {
    text-decoration: none;
    font-weight: 500;
    margin-right: 5px;
}

.comment-user:hover,
.comment-button-hide:hover {
    text-decoration: underline;
}

.comment-time,
.comment-button-hide,
.comment-user {
    font-size: 15px;
    color: rgb(68, 68, 68);
}

.comment-header {
    display: -webkit-flex;
    display: flex;
    margin-top: 10px;
}

.comment-button-hide {
    cursor: pointer;
    margin-left: 10px;
}

.comment-text,
.comment-text p {
    font-size: 14px;
    margin-bottom: 15px;
    overflow: auto;
}

.comments-tree-indicator {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: aqua;
    -webkit-align-self: center;
            align-self: center;
}

.comment-time {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 12px;
}
.page {
    text-decoration: none;
    color: black;
    font-weight: 500;
    width: 40px;
    text-align: center;
    height: 40px;
    line-height: 40px;
    border-radius: 50%;
    transition: all ease-in-out .1s;
}

.page-active{
    color: rgb(119, 119, 119);
    font-weight: 400;
    pointer-events: none;
}

/*.page:hover{
    background-color: rgb(206, 206, 206);
}*/

.loading {
text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
}
.Nav a{
    text-decoration: none;
    color: white;
    padding-right: 10px;
    padding-left: 10px;
    margin-right: 10px;
    margin-left: 10px;
}

.Nav > a:first-of-type{
    font-weight: 700;
    text-decoration: none !important;
}

.Nav .active {
    text-decoration: underline;
}

.Nav {
    background-color: rgb(22, 22, 22);
    height: 30px;
    line-height: 30px;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 111px 1fr; 
}

.disabled {
    pointer-events: none;
}

.nav-separator {
    color: white;
    border-right: 1px solid white; 
}

@media (max-width: 480px) {
    .Nav {
        height: 60px;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr;
    }

    .nav-second {
        font-size: 14px;
    }

    .nav-separator:first-of-type {
        display: none;
    } 
}
.Main {
    padding-left: 5px;
    padding-right: 5px;
}
.Footer {
    background-color: rgb(22, 22, 22);
    height: 30px;
    line-height: 30px;
    color: white;
    text-align: center;
    font-size: 14px;
}
